export default {
	data(){
		return{
			result:[]
		}
	},
	onLoad() {
		this.getBg();
	},
	methods:{
		getBg(){
			var _self = this;
			uni.request({
				url:'https://service.xiaoyuan.net.cn/garbage/index/search?kw=可乐瓶子',
				header:{
					'content-type':'application/x-www-form-urlencoded',
				},
				success:(res) => {
					_self.result = res.data.data[0];
					console.log(_self.result.type);
				}
			})
		}
	}
}
